em;\n  display: flex;\n  flex-direction: column;\n  height: calc(100vh - 58px);\n  background: #fff;\n\n  // border: 2px dashed red;\n}\n\nh1,\nh2,\nh3,\np,\npre,\nul {\n  // margin-bottom: 1.5em;\n}\n\nh3,\nh4 {\n  letter-spacing: 1px;\n  margin-bottom: 1rem;\n  @include tab-land {\n    margin-bottom: 2rem;\n  }\n}\n\n.h3-secondary {\n  color: $clr-seagull;\n  font-size: 2.75rem;\n  @include tab-land {\n    font-size: 3.125rem;\n  }\n}\n\n.h4-secondary {\n  color: #1b70de;\n}\n\nimg {\n  max-width: 100%;\n  display: block;\n}\n\n.wrap {\n  margin-top: 2rem;\n  // background-color: rebeccapurple;\n\n  @include phablet {\n    text-align: center;\n  }\n}\n\nabbr[title] {\n  border-bottom: none;\n  text-decoration: underline;\n  text-decoration: underline dotted;\n}\n","//MEDIA QUERRY MANAGER ---BOOTSTRAP QUERRIES///////////////////\n// landscape phones -- 567px\n\n@mixin phone-land {\n  @media only screen and (min-width: 400px) {\n    @content;\n  }\n}\n@mixin phablet {\n  @media only screen and (min-width: 550px) {\n    @content;\n  }\n}\n@mixin tab-land {\n  @media only screen and (min-width: 750px) {\n    @content;\n  }\n}\n@mixin desktop {\n  @media only screen and (min-width: 1024px) {\n    @content;\n  }\n}\n@mixin desktop-hd {\n  @media only screen and (min-width: 1200px) {\n    @content;\n  }\n}\n\n@mixin phablet-and-down {\n  @media only screen and (max-width: 550px) {\n    @content;\n  }\n}\n@mixin tab-land-and-down {\n  @media only screen and (max-width: 750px) {\n    @content;\n  }\n}\n\n@mixin verticalHeight {\n  @media only screen and (min-height: 75em) {\n    @content;\n  }\n}\n\n@mixin easeOut {\n  transition: all 0.8s ease-out;\n}\n","$mainWhite: #fff;\n$mainBlack: #000;\n\n// Main page colors\n$clr-seagull: #80c5ee; // main Color\n$clr-seagull: #1eaedb; // main Color\n$clr-natgray: #948e83;\n$clr-anthensGray: #948e83;\n$clr-sanJuan: #365c7b;\n$clr-sirocco: #6f8282;\n$clr-lightgray1: #ddd;\n$clr-skeleton: #1eaedb;\n//  #79bae2 #80c5ee;\n\n// link-hoverty ---link hover translate y\n",".ptb-sm {\n  // padding: 0.5rem 0;\n}\n\n.plr-1 {\n  // padding: 0 1rem;\n}\n\n.plr-2 {\n  // padding: 0 2rem;\n}\n.pb-0 {\n  padding-bottom: 0px !important;\n}\n\n.mb-2 {\n  margin-bottom: 2rem;\n}\n\n.mt-2 {\n  // margin-top: 2rem;\n}\n\n.text-center {\n  text-align: center;\n}\n\n.d-inline {\n  display: inline-block;\n}\n\n\n.l-space-1{\n  letter-spacing: 1px;\n}\n.l-space-2{\n  letter-spacing: 2px;\n}\n\n",".header {\n  &--home {\n    background: $clr-seagull;\n    padding: 0.5rem 0;\n\n    @include tab-land {\n      padding: 1rem 0;\n    }\n  }\n}\n.main-nav {\n  &__list {\n    list-style: none;\n    display: flex;\n    justify-content: space-around;\n    margin-bottom: 0; //*overwrites in skeleton\n  }\n\n  &__item {\n    position: relative;\n    margin: 0; //*overwrites skeleton\n\n    &:not(:first-of-type) {\n      @include tab-land {\n        &:not(:last-of-type) {\n          margin-right: 2.5rem;\n        }\n      }\n    }\n  }\n\n  & .d-none-sm {\n    //* hides and reveals KAdaboh(back home btn\n    display: none;\n    @include phablet {\n      display: block;\n      margin-right: auto;\n    }\n  }\n\n  &__link {\n    padding: 0.5rem 0.5rem;\n    @extend %link; //* apply sass placeholder styles\n\n    @include phablet {\n      padding: 1rem;\n    }\n\n    &:focus,\n    &:active {\n      padding: 1rem 0.5rem; //? animates to shrink in size left and right as paddign is changed from 1rem to .5rem l& r || might not be visible on mobile though\n    }\n  }\n}\n//@ SASS PLACHOLDER GLOBAL STYLES FOR LINKS HOVER\n%link {\n  border-radius: 3px;\n  transition: all 250ms; //? transition styles for active and focus state\n\n  &::after {\n    content: \"\";\n    position: absolute;\n    display: block;\n    width: 60%;\n    height: 2px;\n    bottom: 3px;\n    left: 1rem; //* cancells 1rem padding on link(a tag)\n    background-color: $clr-anthensGray;\n\n    transform: scaleX(0);\n    transition: all 250ms ease-out;\n    transform-origin: left;\n  }\n\n  &:hover::after {\n    transform: scaleX(1);\n  }\n\n  &.current {\n    font-weight: bold;\n    color: $clr-seagull;\n  }\n\n  &.current::after {\n    width: 100%;\n    bottom: 0px;\n    transform: scaleX(1);\n  }\n}\n",".intro {\n  &__content {\n    margin-top: 4rem;\n    text-align: center;\n\n    @include phablet {\n      margin-top: 10rem;\n      text-align: center;\n    }\n  }\n  &__heading {\n    & span {\n      border-radius: 3px;\n      background-color: $clr-lightgray1;\n      padding: 0 4px;\n    }\n\n    @include phablet {\n      display: none;\n    }\n  }\n\n  &__description {\n    font-size: 3rem;\n\n    @include tab-land {\n      font-size: 4rem;\n    }\n  }\n\n  &__certs {\n    margin-bottom: auto;\n \n\n    &--title {\n      background-color: $clr-lightgray1;\n      width: max-content;\n      padding: 0 1rem;\n      margin: 2rem auto;\n      border-radius: 3px;\n\n      @include phablet {\n        margin-top: 4rem;\n      }\n    }\n\n    &--img {\n      width: 10rem;\n      height: 10rem;\n\n      @include phablet {\n        width: 15rem;\n        height: 15rem;\n      }\n      &.linuxplus {\n        width: 12rem;\n        height: 12rem;\n        float: left;\n        margin-left: -8px;\n        @include phablet {\n          width: 17rem;\n          height: 17rem;\n        }\n      }\n    }\n\n    & .col {\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      margin: 2rem auto; //*fixes centering bcos of max-width\n   \n      @include phablet {\n        max-width: 550px;\n      }\n    }\n\n  \n    & .date {\n      background-color: $clr-lightgray1;\n      padding: 0.5rem;\n      border-radius: 3px;\n    }\n  }\n\n  &__hello {\n    font-size: 4rem;\n    font-weight: 600;\n    color: $clr-seagull;\n\n    @include tab-land {\n      font-size: 6rem;\n    }\n  }\n}\n",".btn {\n  position: relative;\n  text-align: center;\n  display: inline-flex;\n  justify-content: center;\n  align-items: center